* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
.App {
  text-align: center;
}

.logo {
  margin: 50px auto;
  /* text-align: center; */
}
.logo img {
  width: 320px;
  cursor: pointer;
}

.main {
  width: 100%;
  height: 80vh;
  background: url("./main.jpg") no-repeat;
  background-size: cover;
  margin-bottom: 50px;
}

.product {
  width: 350px;
  margin-bottom: 10px;
}

.product2 {
  width: 300px;
  margin-bottom: 10px;
}

.con1 {
  margin: 100px auto;
}

/* product name */
.title {
  font-size: 18px;
  height: 35px;
}

.con1 h2 {
  text-align: center;
  font-weight: bold;
  margin-bottom: 30px;
}
.con1 h4 {
  font-weight: bold;
  text-align: center;
  margin: 10px;
}

.con1 p {
  text-align: center;
  margin: 3px;
}

.row {
  margin-bottom: 40px;
}

/* detail */

.detail-wrap {
  width: 90%;
  margin: 100px auto;
  display: flex;
  justify-content: space-between;
}

.detail-wrap img {
  width: 50%;
}

.detail-text {
  width: 40%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.detail-text > h2 {
  font-size: 23px;
  white-space: nowrap;
  margin-bottom: 10px;
}
.detail-text > h4 {
  font-weight: normal;
  font-size: 16px;
}
.detail-text > p {
  font-size: 20px;
  margin: 20px 0px;
}

.btn {
  margin-top: 40px;
  display: inline-block;
  cursor: pointer;
  background: #111;
  color: #fff;
  padding: 10px 30px;
}

.btn:hover {
  border: 1px solid #111;
}

.tab {
  width: 80%;
  margin: 0px auto;
}

.tab-inner {
  height: 200px;
}
